/* Responsive simple navbar baby */
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css');
@import url('https://fonts.googleapis.com/css?family=Prompt:300,400,500&subset=latin-ext');
@import url('https://fonts.googleapis.com/icon?family=Material+Icons');


.main-nav {
    position: fixed;
    width: 100%;
    height: 100px;
    top: 0;
    border-bottom: 1px solid #ccc;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    /*align-items: center;*/
    background-color: white;
    color: black;
    transition: all 500ms ease-in-out;
    z-index: 1;
  }
  
  .brand-area {
    margin: 1rem;
    padding-left: 2 rem;
    font-style: normal;
  }
  
  .brand-name {
    text-decoration: none;
    color: black;
    /*font-family: 'Freestyle Script';*/
    font-family: 'Magneto';
    font-size: 42px;
    font-weight: 200;
    cursor: pointer;
    transition: font-size 500ms ease-in-out;
    font-stretch: 100%;
  }
  
  .brand-name:hover {
    text-decoration: none;
    color: black;
  }
  
  .brand-title {
    font: italic 13px/110% 'Old Standard TT';
    color: #999;
    font-weight: 400;
    transition: all 500ms ease-in-out;
  }
  
  .navbar-tabs-container {
    position: relative;
    min-width: 65%;
  }
  
  .button-text {
    margin: auto;
  }
  
  .navbar-tabs ul {
    margin: 0;
    padding: 0;
    display: flex;
    position: absolute;
    right: 5rem;
    bottom: 7px;
  }
  
  .navbar-tabs li {
    list-style: none;
  }
  
  .navbar-tabs li a {
    text-decoration: none;
    color: #999;
    padding-right: 2rem;
    display: block;
    font: bold 95%/100% Arial, Helvetica, sans-serif;
  }
  
  .navbar-tabs li a:hover {
    color: black;
  }
  
  /*
  small navbar
  */
  .small-nav {
    height: 50px;
  }
  
  .small-brand-name {
    font-size: 26px;
  }
  
  .small-brand-title {
    display: none;
  }
  
  /* close navbar  */
  .up {
    transform: translateY(-55px);
  }
  
  /* Social Media Buttons */
  
  .fa {
    display: inline !important;
    padding: 20px;
    font-size: 30px;
    width: 30px;
    text-align: center;
    text-decoration: none;
    margin: 5px 2px;
    border-radius: 50%;
    padding-top: 50px;
  }
  
  .social-med {
    position: absolute;
    transition: all 500ms ease-in-out;
    right: 10rem;
  }
  
  .fa:hover {
    opacity: 0.7;
  }
  
  .fa-facebook {
    background: #3b5998;
    color: white;
  }
  
  .fa-facebook:hover {
    background: #3b5998;
    color: white;
    text-decoration: unset;
  }
  
  .fa-linkedin {
    background: #007bb5;
    color: white;
  }
  
  .fa-linkedin:hover {
    background: #007bb5;
    color: white;
    text-decoration: unset;
  }
  
  .fa-youtube {
    background: #bb0000;
    color: white;
  }
  
  .fa-youtube:hover {
    background: #bb0000;
    color: white;
    text-decoration: unset;
  }
  
  .fa-instagram {
    background: #125688;
    color: white;
  }
  
  .fa-instagram:hover {
    background: #125688;
    color: white;
    text-decoration: unset;
  }
  
  .social-med-none {
    display: none;
  }